<script>

import QRSinhala from './QR_Sinhala.vue';
import QRTamil from './QR_Tamil.vue';
import QREnglish from './QR_English.vue';

export default {
  name: 'LanguageScreen',
  props: {
    msg: String
  },

   components: {
    QRSinhala,
    QRTamil,
    QREnglish
  },

  data() {
      return {
        init: true,
        qr_sinhala: false,
        qr_tamil:false,
        qr_english:false,
        player: new Audio(),
        src: require("../assets/c.mp3")
      }
  },

  methods: {

    play(){
        let note = new Audio(this.src);
      // note.play(); // new Audio will load asynchronously the audio
      // so instead of play directly after create note, you can listen for
      // a event and wait for it to know that the sound was loaded and can be
      // played

      // listen for the canplaythrough event
      note.addEventListener("canplaythrough", () => { 
        /* the audio is now playable; play it if permissions allow */
        note.play(); // the audio now can be played
      });


    },
    qr_scan_sinhala(){
      this.init=false
      this.qr_sinhala=true
    },
      qr_scan_tamil(){
      this.init=false
      this.qr_tamil=true
    },
      qr_scan_english(){
      this.init=false
      this.qr_english=true
    },
    updateState(updatedState){
      console.log(updatedState)
    },

    test(){ // API for requesting audio for calling next patient

       this.axios.get('http://localhost:3000/nextpatient_audio/' +'?patient_id='+1214+'&room_id=4',{responseType: 'arraybuffer'}).then((response) => {
        this.playOutput(response.data)
        }).catch(error => {
        console.log(error)
      })
    },

    playOutput(arrayBuffer){
        var audioContext = new AudioContext();
        var outputSource;
        try {
            if(arrayBuffer.byteLength > 0){
              
                // 2)
                audioContext.decodeAudioData(arrayBuffer,
                function(buffer){
                    // 3)
                    audioContext.resume();
                    outputSource = audioContext.createBufferSource();
                    outputSource.connect(audioContext.destination);
                    outputSource.buffer = buffer;
                    outputSource.start(0);
                },
                function(){
                    console.log(arguments);
                });
            }
    } catch(e) {
        console.log(e);
    }
    }

  },
  created(){
    console.log('HELLLO')
  }
}
</script>

