<template>
<v-container fluid >

  <div style="padding:30px" v-if="qrview">
        <v-btn
            color="#254987"
            dark
            rounded
            height="60px"
            width="120px"
            @click="reload"
          >
            <v-icon
              left
            >
              mdi-arrow-left
            </v-icon> Back
          </v-btn>

 <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      width="120px"
      height="60px"
      color="red"
    >
      <v-row  justify="space-around" align="center">
        {{text}}
    </v-row>
    </v-snackbar>

     </div>

<v-container fluid  v-if="qrview">
    <v-row  justify="space-around" align="center">
        <h1 class="english"> Please scan your QR code </h1>
    </v-row>
    <v-row  justify="space-around" align="center">
      <v-card height="400px" width="400px">
          <qrcode-stream @decode="onDecode"></qrcode-stream>
          <h1>{{qrString}}</h1>
      </v-card>
    </v-row>
</v-container>

<CompletionSinhala v-if="!qrview"  v-bind:success="success"/>

</v-container>
</template>

<script>
import CompletionEnglish from './Completion_English.vue';


export default {
  name: 'QR_English',
  props: {
    msg: String
  },
  components: {
    CompletionEnglish
  },
  data() {
      return {
          qrString: "",
          tokenNum:"",
          roomNum:"",
          patientName:"",
          qrview:true,
          success:true,
          player: new Audio(),
          beep_src: require('../assets/Beep.mp3'),
          snackbar: false,
          text: 'Server connection failed - CONNREFUSED',
          timeout: 2000,

      }
  },

  methods: {

      reload(){
        location.reload()
      },

      onDecode (decodedString) {
        this.qrString=decodedString
        if(decodedString != ''){
        this.player.src = this.beep_src;
        this.player.play()

        this.axios.get('http://localhost:3000/gentoken/' +'?patient_id='+this.qrString).then((response) => {

        
        console.log(response)

        this.success = true
        this.qrview = false

        this.$emit('updateState','Helloooo')
    

        }).catch(error => {

            this.snackbar=true;

        console.log(error)
        }).finally(() => {

          setTimeout(() => {
           location.reload() 
          }, 5000);

      })
        }
      
  }
  }
};
</script>


<style scoped>

.english {
 font-size: 70px;
 padding-bottom: 80px;
 color:white

}

</style>
