<template>
<v-container fluid >

  <div style="padding:30px" v-if="qrview">
        <v-btn
            color="#254987"
            dark
            rounded
            height="60px"
            width="120px"
            @click="reload"
          >
            <v-icon
              left
            >
              mdi-arrow-left
            </v-icon> Back
          </v-btn>

 <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      width="120px"
      height="60px"
      color="red"
    >
      <v-row  justify="space-around" align="center">
        {{text}}
    </v-row>
    </v-snackbar>

     </div>

<v-container fluid  v-if="qrview">
    <v-row  justify="space-around" align="center">
        <h1 class="sinhala2"> ඔබගේ කේතය පහත අයුරින් කැමරාවට යොමු කරන්න </h1>
    </v-row>
    <v-row  justify="space-around" align="center">
      <v-card height="400px" width="400px">
          <qrcode-stream @decode="onDecode"  @init="onInit"/> </qrcode-stream>
          <h1>{{qrString}}</h1>
      </v-card>
    </v-row>
</v-container>

<CompletionSinhala v-if="!qrview"  v-bind:success="success"/>

</v-container>
</template>

<script>
import CompletionSinhala from './Completion_Sinhala.vue';


export default {
  name: 'QR_Sinhala',
  props: {
    msg: String
  },
  components: {
    CompletionSinhala
  },
  data() {
      return {
          qrString: "",
          tokenNum:"",
          roomNum:"",
          patientName:"",
          qrview:true,
          success:true,
          player: new Audio(),
          beep_src: require('../assets/Beep.mp3'),
          snackbar: false,
          text: 'Server connection failed - CONNREFUSED',
          timeout: 2000,
          error: ''

      }
  },

  async onInit (promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = "ERROR: you need to grant camera access permisson"
        } else if (error.name === 'NotFoundError') {
          this.error = "ERROR: no camera on this device"
        } else if (error.name === 'NotSupportedError') {
          this.error = "ERROR: secure context required (HTTPS, localhost)"
        } else if (error.name === 'NotReadableError') {
          this.error = "ERROR: is the camera already in use?"
        } else if (error.name === 'OverconstrainedError') {
          this.error = "ERROR: installed cameras are not suitable"
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = "ERROR: Stream API is not supported in this browser"
        }
      }
    },

  methods: {

      reload(){
        location.reload()
      },

      onDecode (decodedString) {
        this.qrString=decodedString

        this.player.src = this.beep_src;
        this.player.play()

        this.axios.get('http://localhost:3000/gentoken/' +'?patient_id='+this.qrString).then((response) => {

        
        console.log(response)

        this.success = true
        this.qrview = false

        this.$emit('updateState','Helloooo')
    

        }).catch(error => {

            this.snackbar=true;

        console.log(error)
        }).finally(() => {

          setTimeout(() => {
           location.reload() 
          }, 5000);

      })
      
  }
  }
};
</script>


<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Abhaya+Libre:wght@500&display=swap');

.sinhala2 {
 font-size: 50px;
 padding-bottom: 80px; 
 color:white
}

</style>
