<template>
<v-container fluid >

  <div style="padding:30px" v-if="qrview">
        <v-btn
            color="#254987"
            dark
            rounded
            height="60px"
            width="120px"
            @click="reload"
          >
            <v-icon
              left
            >
              mdi-arrow-left
            </v-icon> Back
          </v-btn>

 <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      width="120px"
      height="60px"
      color="red"
    >
      <v-row  justify="space-around" align="center">
        {{text}}
    </v-row>
    </v-snackbar>

     </div>

<v-container fluid  v-if="qrview">
    <v-row  justify="space-around" align="center">
        <h1 class="tamil2">நீங்கள் QR குறியீட்டை ஸ்கேன் செய்யுங்கள் </h1>
    </v-row>
    <v-row  justify="space-around" align="center">
      <v-card height="400px" width="400px">
          <qrcode-stream @decode="onDecode"></qrcode-stream>
          <h1>{{qrString}}</h1>
      </v-card>
    </v-row>
</v-container>

<CompletionSinhala v-if="!qrview"  v-bind:success="success"/>

</v-container>
</template>

<script>
import CompletionTamil from './Completion_Tamil.vue';


export default {
  name: 'QR_Tamil',
  props: {
    msg: String
  },
  components: {
    CompletionTamil
  },
  data() {
      return {
          qrString: "",
          tokenNum:"",
          roomNum:"",
          patientName:"",
          qrview:true,
          success:true,
          player: new Audio(),
          beep_src: require('../assets/Beep.mp3'),
          snackbar: false,
          text: 'Server connection failed - CONNREFUSED',
          timeout: 2000,

      }
  },

  methods: {

      reload(){
        location.reload()
      },

      onDecode (decodedString) {
        this.qrString=decodedString

        this.player.src = this.beep_src;
        this.player.play()

        this.axios.get('http://localhost:3000/gentoken/' +'?patient_id='+this.qrString).then((response) => {

        
        console.log(response)

        this.success = true
        this.qrview = false

        this.$emit('updateState','Helloooo')
    

        }).catch(error => {

            this.snackbar=true;

        console.log(error)
        }).finally(() => {

          setTimeout(() => {
           location.reload() 
          }, 5000);

      })
      
  }
  }
};
</script>


<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Abhaya+Libre:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abhaya+Libre:wght@800&family=Catamaran:wght@900&display=swap');

.tamil2 {
 /* font-family: 'Abhaya Libre', sans-serif; */
  font-family: 'Catamaran', sans-serif;
 font-size: 40px;
 padding: 40px;

}

</style>
